/* Ensure the body takes up the full viewport height and prevents scrolling */
body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden; /* Prevent scrolling */
}

/* Center the SVG in the viewport */
.hexagon-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Full viewport height */
  width: 100vw; /* Full viewport width */
  overflow: hidden; /* Ensure no overflow */
}

.hexagon-svg {
  width: 100%; /* Full width */
  height: auto; /* Maintain aspect ratio */
  max-width: 600px; /* Optional: Limit maximum width for better visuals */
}

.hexagon {
  stroke: rgba(255, 255, 255, 0.2);
}

.logo {
  filter: invert(100%);
}

.DIAMOND {
  cursor: pointer;
}

/* Media Queries */
@media (min-width: 1024px) {
  /* For screens larger than 1024px (e.g., laptops) */
  .hexagon-svg {
    width: 30%; /* Adjust as needed */
  }
}

@media (max-width: 1024px) {
  /* For screens smaller than or equal to 1024px (e.g., tablets and phones) */
  .hexagon-svg {
    width: 100%;
  }
}
